html {
  background-color: #e4e4e4;
}

.container {
  position: relative;
  width: 100vw;
  height: 100vh;
}

.container .loading {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 26px;
}

.moviesContainer {
  padding: 0 50px;
}

.moviesContainer #logo {
  text-align: center;
  font-size: 40px;
  font-weight: 700;
  margin: 100px 0 140px;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}
